import React, { useState, useEffect } from 'react';
import { Input, AutoComplete, Modal } from 'antd';

import { getListAnime } from '../../api/anime';
import DetailAnime from '../anime/DetailAnime';

import '../../css/searchbar.css';

const Searchbar = () => {

    const[animeList, setAnimeList] = useState([]);
    const[slug, setSlug] = useState();
    const [isModalVisible, setIsModalVisible] = useState(false);
    
    useEffect(() => {
        async function getAnimeList() {
            const anime = await getListAnime();
            const animeForSearch = anime.data.map(function(item) {
                return {
                    key: item.id,
                    slug: item.slug,
                    value: item.title
                }
            });
            setAnimeList(animeForSearch);
        }

        getAnimeList();
    }, []);

    const showModal = () => {
        setIsModalVisible(true);
      };
    
      const handleOk = () => {
        setIsModalVisible(false);
      };
    
      const handleCancel = () => {
        setIsModalVisible(false);
      };

	const onSelect = (value, anime) => {
        console.log('onSelect', value);
		console.log(anime.slug)
	};

	const onSearch = (value, id) => {
		console.log(value)
	};

    return (
        <AutoComplete
            dropdownMatchSelectWidth = { 252 } className = 'searchbar' options = { animeList } 
            filterOption = {(inputValue, option) =>
                option.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
            } onSelect = { onSelect }
        >
            <Input.Search 
                allowClear placeholder = 'Search Here' 
                enterButton onSearch = { onSearch }
            />
        </AutoComplete>
    );
}

export default Searchbar;